@mixin transformFix {
    -webkit-backface-visibility: hidden;
    -webkit-transform: translateZ(0) scale(1.0, 1.0);
    transform: translateZ(0);
}


@mixin textBgColor {
    display: inline-block;
    background-image: linear-gradient(rgba(0, 0, 0, 0.596),rgba(0, 0, 0, 0.664)),  url(../assets/num.jpg);
    -webkit-background-clip: text;
    color: transparent;
}

@mixin flexCenter {
    display: flex;
    justify-content: center;
    align-items: center;
    
}